*{margin:0;padding:0;box-sizing:border-box;}
body {font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',sans-serif; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; font-size:1em; line-height:1.5em;background:#eee;color:#000;}
code {font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;}
header,footer,section,aside,article,menu,nav{display:block;}
ul,ol{list-style:none;}
ul.default{font-size:1em; list-style-type:decimal; margin:15px 0 15px 20px;}
ul.default > li{margin-bottom:5px;}
ol.default{font-size:1em; list-style-type:disc; margin:15px 0 15px 20px;}
ol.default > li{margin-bottom:5px;}
a{color:#08c;text-decoration:none;}

/*grid css*/
.container{max-width:1200px;padding-left:15px;padding-right:15px;margin-left:auto;margin-right:auto;}
.container-full{width:100%;padding-left:15px;padding-right:15px;}
.row{display:flex;flex-wrap:wrap;margin-left:-15px;margin-right:-15px;}
.form-row{display:flex;flex-wrap:wrap;margin-left:-5px;margin-right:-5px;}
[class^="col-"]{padding-left:15px;padding-right:15px;}
.form-row .col[class^="col-"]{padding-left:5px;padding-right:5px;}
.col-12{width:100%;}
.col-8{width:66.66%;}
.col-6{width:50%;}
.col-4{width:33.33%;}
.col-3{width:25%;}

/*App css*/
.form-app{height:calc(100vh);background:#0099ff}
.header-main{padding:15px;text-align:center;border-bottom:1px solid #ddd;}
.header-main h1{font-size:30px;font-weight:bold;line-height:35px;}
.page-body{display:flex;flex-wrap:wrap;}
.page-nav{width:350px;height:calc(100vh - 66px); padding:30px; overflow-y:auto;}
.page-nav .navbar > li{margin-bottom:15px;}
.page-nav .navbar > li:last-child{border-bottom:none;}
.page-nav .navbar > li > a{display:block;position:relative;padding:10px 15px;font-size:16px; line-height:24px; background:#166c97;color:#fff;border-radius:5px;}
.page-nav .navbar > li > a::after{content:'';position:absolute;right:-20px; top:12px; border:10px solid transparent; border-left-color:#166c97;}
.page-nav .navbar > li > a.active{background:#fff;color:#08c;}
.page-nav .navbar > li > a.active::after{border-left-color:#fff;}
.page-routing-area{width:calc(100% - 350px); padding:30px; overflow-x:hidden;overflow-y:auto; height:calc(100vh - 66px); background:#0099ff;}
.page-routing-area::-webkit-scrollbar{width:10px;}
.page-routing-area::-webkit-scrollbar-track {background:#ccc;}
.page-routing-area::-webkit-scrollbar-thumb {background:#666;}
.form-panel{background:#fff;border-radius:10px;box-shadow:1px 0px 15px #999;}
.form-panel-h{padding:20px;border-bottom:1px solid #ddd; text-align:center;}
.form-panel-h h2{font-size:25px;color:#08c; line-height:30px;}
.form-panel-b{padding:20px;}
.form-panel-f{padding:20px;text-align:center; border-top:1px solid #ddd;}
.form-label{font-size:14px;font-weight:500;color:#000;display:block;margin-bottom:5px;}
.form-group{margin-bottom:20px;}
.form-control{width:100%; padding:10px;border:1px solid #ddd;border-radius:5px; font-size:14px; line-height:20px; outline:none;box-shadow:none;}
.btn{padding:7px 30px;border:1px solid #ccc;background:#fff;border-radius:5px;color:#333;font-weight:500; margin:0 5px;cursor: pointer; outline:none;box-shadow:none;}
.btn.btn-primary{background:#08c;color:#fff;border-color:#08c;}
